import { createRouter, createWebHashHistory } from 'vue-router'
import HomePage from '../views/HomePage/HomePage'

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage
  },
  {
    path: '/fee-schedule',
    name: 'FeeSchedule',
    component: () => import('@/views/FeeSchedule/FeeSchedule')
  },
  {
    path: '/maintenance',
    name: 'Maintenance',
    component: () => import('@/views/UnderMaintenance')
  },
  {
    path: '/exchange-confirmation',
    name: 'ExchangeConfirmation',
    component: () => import('@/views/ExchangeConfirmation/ExchangeConfirmation')
  },
  {
    path: '/deposit',
    name: 'Deposit',
    component: () => import('@/views/DepositPage/DepositPage')
  },
  {
    path: '/about-us',
    name: 'AboutUs',
    component: () => import('@/views/AboutUs/AboutUs')
  },
  {
    path: '/faq-aml',
    name: 'FaqAndAml',
    component: () => import('@/views/FaqAndAml/FaqAndAml')
  },
  {
    path: '/:catchAll(.*)',
    name: '404',
    component: () => import('@/views/404')
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
