import coinsMock from '@/assets/coinsMock'

export default {
  namespaced: true,
  state: {
    coinsList: coinsMock,
    sendCoin: coinsMock[0],
    receiveCoin: coinsMock[1]
  },

  mutations: {
    SET_SEND_COIN (state, payload) {
      state.sendCoin = payload
    },
    SET_RECEIVE_COIN (state, payload) {
      state.receiveCoin = payload
    }
  },

  actions: {
    setSendCoin ({ commit }, payload) {
      commit('SET_SEND_COIN', payload)
    },
    setReceiveCoin ({ commit }, payload) {
      commit('SET_RECEIVE_COIN', payload)
    }
  },

  getters: {
    getSendCoin (state) {
      return state.sendCoin
    },
    getReceiveCoin (state) {
      return state.receiveCoin
    },
    getCoinsList (state) {
      return state.coinsList
    }
  }
}
