<template>
  <footer class="footer">
    <nav class="footer__navigation">
      <ul class="footer__navigation__list">
        <li
          v-for="item of links"
          :key="item.key"
          class="footer__navigation__list__item"
        >
          <a @click="linkClick(item)">{{ item.title }}</a>
        </li>
      </ul>
    </nav>
    <div class="footer__tools">
      <div
        v-for="item of tools"
        :key="item.key"
        class="footer__tools__item"
      >
        <Icon :name="item.icon"/>
      </div>
      <Select
        v-model="lang"
        :options="selectOptions"
        class="lang"
      >
        <template #selected>
          <div class="lang__selected">
            <Icon :name="lang.label"/>
          </div>
        </template>
        <template v-slot:option="{ option }">
          <Icon :name="option.label" />
        </template>
      </Select>
    </div>
  </footer>
</template>

<script>
import { reactive, ref, watch, computed } from 'vue'
import { useRouter } from 'vue-router'
import { useI18n } from 'vue-i18n'
import Icon from '@/components/Icon/Icon'
import Select from '@/components/UIKit/Select'

export default {
  name: 'Footer',
  components: { Select, Icon },
  setup () {
    const { t, locale } = useI18n()
    const router = useRouter()
    const linkClick = item => {
      if (item.to) {
        router.push(item.to)
      }
    }

    const links = computed(() => ([
      { title: t('footer.aboutUs'), key: 'about', to: '/about-us' },
      { title: 'AML/FAQ', key: 'faq', to: '/faq-aml' },
      { title: t('footer.news'), key: 'news' }
    ]))
    const tools = reactive([
      { title: 'Twitter', icon: 'twitter', key: 'twitter' },
      { title: 'Telegram', icon: 'telegram', key: 'telegram' }
    ])

    const selectOptions = ref([
      {
        label: 'uk',
        value: 'en'
      },
      {
        label: 'ru',
        value: 'ru'
      }
    ])

    const lang = ref({
      label: 'uk',
      value: 'en'
    })

    watch(lang, (nv) => {
      locale.value = nv.value
    })

    return {
      links,
      tools,
      lang,
      selectOptions,
      linkClick
    }
  }
}
</script>

