<template>
  <div
    v-html="icons[name]"
    class="icon"
    :style="styles"
  />
</template>

<script>
import icons from '@/assets/icons'
import { reactive } from 'vue'

export default {
  name: 'Icon',
  props: {
    name: {
      type: String,
      validate (val) {
        const keys = Object.keys(icons)
        return keys.includes(val)
      }
    },
    width: {
      type: [String, Number],
      default: null
    },
    height: {
      type: [String, Number],
      default: null
    }
  },
  setup (props) {
    const setStyles = () => {
      const styles = {}
      if (props.height) {
        styles['--height'] = props.height
      }
      if (props.height) {
        styles['--width'] = props.width
      }
      return styles
    }
    const styles = reactive(setStyles())
    return {
      icons,
      styles
    }
  }
}
</script>

<style lang="scss">
  .icon {

    svg {
      display: block;
      height: var(--height);
      width: var(--width);
    }
  }

</style>
