<template>
  <Container variant="label" class="input" :class="{ input__error: error, 'input__read-only': readOnly }">
    <div v-if="$slots.prepend" class="input__prepend">
      <slot name="prepend" />
    </div>
    <div class="input__body">
      <span class="input__body-label">{{ label }}</span>
      <input
        class="input__body-field"
        :readonly="readOnly"
        :disabled="disabled"
        :type="type"
        :value="modelValue"
        :placeholder="placeholder"
        @input="$emit('update:modelValue', $event.target.value)"
      />
    </div>
    <div v-if="$slots.append" class="input__append">
      <slot name="append" />
    </div>
    <div
      v-if="errorText && error"
      class="input__error-text"
    >
      {{ errorText }}
    </div>
  </Container>
</template>

<script>

import Container from '@/components/Container'
export default {
  name: 'Input',
  components: { Container },
  inheritAttrs: true,
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'text'
    },
    label: {
      type: String,
      default: ''
    },
    modelValue: {
      type: [String, Number],
      default: ''
    },
    error: {
      type: Boolean,
      default: false
    },
    errorText: {
      type: String,
      default: ''
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="scss">
.input {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 14px 9px 20px;

  &__body {
    width: 100%;

    &-label {
      display: block;

      font-size: 14px;
      line-height: 16px;

      color: #000000;
      opacity: 0.4;
    }
    &-field {
      width: 100%;
      margin-left: -3px;
      border: none;
      outline: none;

      font-size: 14px;
      line-height: 16px;

      color: #000000;
      padding-top: 4px;
      background: transparent;

      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
      }
      &[type=number] {
        -moz-appearance: textfield;
      }
    }
  }
  &__prepend {
    margin-right: 10px;
  }
  &__error {
    border: 1px solid map-get($colors, "error");
    box-shadow: $boxShadowError;

    &-text {
      position: absolute;
      top: calc(100% + 6px);
      left: 0;

      font-size: 12px;
      line-height: 14px;
      color: map-get($colors, "error");
    }
  }
}
.input__read-only.input {
  background-color: #FAFAFA;
  box-shadow: none;

  &__body {
    &-field {
      &:before {
        content:'';
        width:100%;
        height:100%;
        position:absolute;
        left:0;
        top:0;
        background:linear-gradient(transparent 150px, white);
      }
    }
  }
}
